export default {
  name:'three_robot_config',
  data() {
    return {
      namelist:[],
      activeName: "1",
      ruleForm: {
        count_range: "",
        coins_range: "",
        leave_rate: "",
        system_bet_min: "",
        system_last_bet_rate: "",
        system_bet_area: "",
        player_bet_min: "",
        player_last_bet_rate: "",
        player_bet_area: "",
        bet_area_float: "",
        vip_seat_rate: ""
      },
      rules: {
        count_range: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        coins_range: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        leave_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        system_bet_min: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        system_last_bet_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        system_bet_area: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        player_bet_min: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        player_last_bet_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        player_bet_area: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        bet_area_float: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        vip_seat_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ]
      },
      resData: {},
      id: 0,
      keys: "",
      loading: false,
      card_compare_value: ""
    };
  },

  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.Getbrnn_robot_control({
      key: "brnn_robot_control.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.resData = res.brnn_normal.robot_type_list;
    this.card_compare_value = res.brnn_normal.card_compare_value;
    Object.keys(this.resData).forEach((item,index)=>{
      this.namelist.push(item)
      if(index === 0){
        this.ruleForm = this.resData[item];
      }
    })
    // console.log(this.resData, this.ruleForm);
  },

  methods: {
    //切换tab栏
    handleClick(tab) {
      // console.log(tab.name);
      Object.keys(this.resData).forEach(item => {
        if (item === tab.name) {
          this.ruleForm = this.resData[item];
        }
      });
    },

    //确认
    submitForm(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid && this.card_compare_value !== "") {

          let res = {
            brnn_normal: {
              card_compare_value:this.card_compare_value,
              robot_type_list:this.resData
            }
          };
           console.log(res);
          if (type === 1) {
            let { data } = await this.$http.HallFunConfig.Putbrnn_robot_control({
              keys: this.keys,
              values: JSON.stringify(res),
              id: this.id
            });
            if (data.code === 1 && data.msg === "ok") {
  
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }else{
            this.$message({
              type: "warning",
              message: "保存失败!"
            });
          }
          } else if (type === 2) {
            this.loading = true;
            let { data } = await this.$http.HallFunConfig.Postbrnn_robot_control({
              keys: this.keys,
              values: JSON.stringify(res),
              id: this.id
            });
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            }else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "发送服务器配置失败!"
              });
            }
          }
        } else {
          this.$message({
            type: "warning",
            message: "所有数据都必须填写!"
          });
          return false;
        }
      });
    }
  }
};



